<template>
	<view class="chat-container">

		
		<!-- È°∂ÈÉ®Ê†áÈ¢òÊ†è -->
		<view class="header">
			<view class="header-left">
				<view class="header-btn" @tap="showHistorySidebar">
					<view class="hamburger-icon">
						<view class="line"></view>
						<view class="line"></view>
					</view>
				</view>
			</view>
			<view class="header-center">
				<text class="title">Êô∫ÊÖßÂÆ≥Ëô´AIÈóÆÁ≠îÂä©Êâã</text>
			</view>
			<view class="header-right">
				<view class="header-btn" @tap="createNewChat">
					<text class="header-icon">+</text>
				</view>
			</view>
		</view>
		
		<!-- ÊêúÁ¥¢Ê†è -->
		<view class="search-container">
			<view class="search-box">
				<view class="search-icon">üîç</view>
				<input 
					class="search-input" 
					v-model="searchKeyword" 
					placeholder="ÊêúÁ¥¢ÂØπËØùÂÜÖÂÆπ..."
					@input="onSearchInput"
					@confirm="performSearch"
				/>
				<view v-if="searchKeyword" class="clear-btn" @tap="clearSearch">
					<text class="clear-icon">√ó</text>
				</view>
			</view>
		</view>
		
		<!-- ËÅäÂ§©Ê∂àÊÅØÂàóË°® -->
		<scroll-view 
			class="chat-list" 
			scroll-y="true" 
			:scroll-top="scrollTop"
			:scroll-into-view="scrollIntoView"
			:show-scrollbar="false"
		>
			<!-- Ê¨¢ËøéÁïåÈù¢ -->
			<view v-if="messageList.length === 0" class="welcome-container">
				<view class="welcome-content">
					<text class="welcome-title">Ê¨¢Ëøé‰ΩøÁî®Êô∫ÊÖßÂÆ≥Ëô´AIÈóÆÁ≠îÂä©Êâã</text>
					<text class="welcome-subtitle">ÊàëÂèØ‰ª•Â∏ÆÊÇ®ËØÜÂà´ÂêÑÁßçÂÆ≥Ëô´Âπ∂Êèê‰æõ‰∏ì‰∏öÂª∫ËÆÆ</text>
					<view class="welcome-features">
						<view class="feature-item">
							<text class="feature-text">Êô∫ËÉΩËØÜÂà´ÂÆ≥Ëô´</text>
						</view>
						<view class="feature-item">
							<text class="feature-text">‰∏ì‰∏öÈò≤Ê≤ªÂª∫ËÆÆ</text>
						</view>
						<view class="feature-item">
							<text class="feature-text">ÊãçÁÖß‰∏ä‰º†ËØÜÂà´</text>
						</view>
					</view>
				</view>
			</view>
			
			<view 
				v-for="(message, index) in messageList" 
				:key="index"
				:class="['message-item', message.type === 'user' ? 'user-message' : 'ai-message', isMessageHighlighted(message) ? 'highlighted-message' : '']"
				:id="'msg-' + index"
			>
				<view class="message-content">
					<view class="avatar">
						<image 
							:src="message.type === 'user' ? userAvatar : aiAvatar" 
							class="avatar-img"
						></image>
					</view>
					<view class="message-bubble">
						<text v-if="!message.image" class="message-text">{{ message.content }}</text>
						<image v-if="message.image" :src="message.image" class="message-image" mode="aspectFit"></image>
						<text class="message-time">{{ message.time }}</text>
					</view>
				</view>
			</view>
			
			<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
			<view v-if="isLoading" class="loading-message">
				<view class="message-content">
					<view class="avatar">
						<image :src="aiAvatar" class="avatar-img"></image>
					</view>
					<view class="message-bubble">
						<view class="typing-indicator">
							<view class="dot"></view>
							<view class="dot"></view>
							<view class="dot"></view>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>
		
		<!-- Â∫ïÈÉ®ËæìÂÖ•Âå∫Âüü -->
		<view class="input-area">
			<view class="input-container">
				<button 
					class="plus-btn" 
					@tap="showImageOptions"
					:disabled="isLoading"
				>
					<text class="plus-icon">+</text>
				</button>
				<input 
					class="message-input" 
					v-model="inputMessage" 
					placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈóÆÈ¢ò..."
					:disabled="isLoading"
					@confirm="sendMessage"
				/>
				<button 
					class="send-btn" 
					:disabled="!inputMessage.trim() || isLoading"
					@tap="sendMessage"
				>
					ÂèëÈÄÅ
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				messageList: [], // Ê∂àÊÅØÂàóË°®
				inputMessage: '', // ËæìÂÖ•Ê°ÜÂÜÖÂÆπ
				isLoading: false, // ÊòØÂê¶Ê≠£Âú®Âä†ËΩΩ
				scrollTop: 0, // ÊªöÂä®‰ΩçÁΩÆ
				scrollIntoView: '', // ÊªöÂä®Âà∞ÊåáÂÆöÊ∂àÊÅØ
				userAvatar: '/static/logo.png', // Áî®Êà∑Â§¥ÂÉè
				aiAvatar: '/static/logo.png', // AIÂ§¥ÂÉè
				currentChatId: null, // ÂΩìÂâçÂØπËØùID
				historyChats: [], // ÂéÜÂè≤ÂØπËØùÂàóË°®
				searchKeyword: '', // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
				searchResults: [], // ÊêúÁ¥¢ÁªìÊûú
			}
		},
		onLoad() {
			// È°µÈù¢Âä†ËΩΩÊó∂ÂàùÂßãÂåñÊñ∞ÂØπËØùÁä∂ÊÄÅ
			this.messageList = [];
			this.currentChatId = 'chat' + Date.now();
			this.inputMessage = '';
		},
		onShow() {
			// Ê£ÄÊü•ÊòØÂê¶Êúâ‰ªéÂéÜÂè≤È°µÈù¢‰º†ÈÄíËøáÊù•ÁöÑÂØπËØùÊï∞ÊçÆ
			const selectedChat = uni.getStorageSync('selectedChat');
			if (selectedChat) {
				console.log('Ê£ÄÊµãÂà∞ÂéÜÂè≤ÂØπËØùÊï∞ÊçÆ:', selectedChat);
				this.loadChat(selectedChat);
				uni.removeStorageSync('selectedChat');
			} else {
				// Â¶ÇÊûúÊ≤°ÊúâÈÄâÊã©ÂéÜÂè≤ÂØπËØùÔºåÁ°Æ‰øùÊòØÊñ∞ÁöÑÂØπËØùÁä∂ÊÄÅ
				if (this.messageList.length === 0) {
					this.currentChatId = 'chat' + Date.now();
				}
			}
		},
		
		onHide() {
			// È°µÈù¢ÈöêËóèÊó∂‰øùÂ≠òÂΩìÂâçÂØπËØù
			if (this.messageList.length > 0) {
				this.saveCurrentChat();
			}
		},
		methods: {
			// ÊòæÁ§∫ÂéÜÂè≤‰æßËæπÊ†è
			showHistorySidebar() {
				console.log('showHistorySidebar Ë¢´Ë∞ÉÁî®');
				// Ë∑≥ËΩ¨Âà∞ÂéÜÂè≤È°µÈù¢
				uni.navigateTo({
					url: '/pages/chatAI/history'
				});
			},
			
			// Âä†ËΩΩÊåáÂÆöÂØπËØù
			loadChat(chat) {
				console.log('Âä†ËΩΩÂéÜÂè≤ÂØπËØù:', chat);
				this.currentChatId = chat.id;
				this.messageList = [...chat.messages];
				this.$nextTick(() => {
					// Âª∂ËøüÊªöÂä®Á°Æ‰øùDOMÂÆåÂÖ®Ê∏≤Êüì
					setTimeout(() => {
						this.scrollToBottom();
					}, 100);
				});
				
				// ÊòæÁ§∫Âä†ËΩΩÊàêÂäüÊèêÁ§∫
				uni.showToast({
					title: 'ÂØπËØùÂä†ËΩΩÊàêÂäü',
					icon: 'success',
					duration: 1500
				});
			},
			
			// ÊòæÁ§∫ÂéÜÂè≤ÂØπËØùÂàóË°®
			showHistory() {
				uni.showToast({
					title: 'ÂéÜÂè≤ÂØπËØùÂäüËÉΩÂºÄÂèë‰∏≠',
					icon: 'none'
				});
				// ËøôÈáåÂèØ‰ª•Ë∑≥ËΩ¨Âà∞ÂéÜÂè≤ÂØπËØùÈ°µÈù¢ÊàñÊòæÁ§∫ÂéÜÂè≤ÂØπËØùÂàóË°®
			},
			
			// ÂàõÂª∫Êñ∞ÂØπËØù
			createNewChat() {
				// Â¶ÇÊûúÂΩìÂâçÊúâÂØπËØùÂÜÖÂÆπÔºåËØ¢ÈóÆÊòØÂê¶‰øùÂ≠ò
				if (this.messageList.length > 0) {
					uni.showModal({
						title: 'ÂàõÂª∫Êñ∞ÂØπËØù',
						content: 'Á°ÆÂÆöË¶ÅÂºÄÂßãÊñ∞ÁöÑÂØπËØùÂêóÔºüÂΩìÂâçÂØπËØùÂ∞ÜË¢´‰øùÂ≠ò„ÄÇ',
						success: (res) => {
							if (res.confirm) {
								// ‰øùÂ≠òÂΩìÂâçÂØπËØùÂà∞ÂéÜÂè≤ËÆ∞ÂΩï
								this.saveCurrentChat();
								this.resetToNewChat();
							}
						}
					});
				} else {
					// Â¶ÇÊûúÂΩìÂâçÊ≤°ÊúâÂØπËØùÂÜÖÂÆπÔºåÁõ¥Êé•ÂàõÂª∫Êñ∞ÂØπËØù
					this.resetToNewChat();
				}
			},
			
			// ÈáçÁΩÆ‰∏∫Êñ∞ÂØπËØù
			resetToNewChat() {
				this.messageList = [];
				// ÁîüÊàêÊñ∞ÁöÑÂØπËØùIDÔºåÈÅøÂÖçÈáçÂ§ç
				this.currentChatId = 'chat' + Date.now();
				this.inputMessage = '';
				uni.showToast({
					title: 'Â∑≤ÂàõÂª∫Êñ∞ÂØπËØù',
					icon: 'success'
				});
			},
			
			// ‰øùÂ≠òÂΩìÂâçÂØπËØù
			saveCurrentChat() {
				if (this.messageList.length === 0) return;
				
				// Ëé∑ÂèñÂØπËØùÊ†áÈ¢òÔºà‰ΩøÁî®Á¨¨‰∏ÄÊù°Áî®Êà∑Ê∂àÊÅØÔºâ
				const userMessages = this.messageList.filter(msg => msg.type === 'user');
				let title = 'Êñ∞ÂØπËØù';
				if (userMessages.length > 0) {
					const firstUserMessage = userMessages[0];
					title = firstUserMessage.content.length > 20 
						? firstUserMessage.content.substring(0, 20) + '...' 
						: firstUserMessage.content;
				}
				
				// Á°Æ‰øùÊúâÂîØ‰∏ÄÁöÑÂØπËØùID
				const chatId = this.currentChatId || 'chat' + Date.now();
				
				let historyChats = uni.getStorageSync('historyChats') || [];
				const existingChat = historyChats.find(chat => chat.id === chatId);
				
				// Ê£ÄÊü•ÊòØÂê¶ÊúâÊñ∞Ê∂àÊÅØ
				let hasNewMessages = false;
				let lastTime = this.getCurrentDateTime();
				
				if (existingChat) {
					// Â¶ÇÊûúÂØπËØùÂ∑≤Â≠òÂú®ÔºåÊ£ÄÊü•Ê∂àÊÅØÊï∞ÈáèÊòØÂê¶Â¢ûÂä†
					if (this.messageList.length > existingChat.messages.length) {
						hasNewMessages = true;
						lastTime = this.getCurrentDateTime(); // ÊúâÊñ∞Ê∂àÊÅØÊó∂Êõ¥Êñ∞Êó∂Èó¥
					} else {
						// Ê≤°ÊúâÊñ∞Ê∂àÊÅØÔºå‰øùÊåÅÂéüÊó∂Èó¥
						lastTime = existingChat.lastTime;
					}
				} else {
					// Êñ∞ÂØπËØùÔºå‰ΩøÁî®ÂΩìÂâçÊó∂Èó¥
					hasNewMessages = true;
				}
				
				const newChat = {
					id: chatId,
					title: title,
					lastTime: lastTime,
					messages: [...this.messageList]
				};
				
				const existingIndex = historyChats.findIndex(chat => chat.id === chatId);
				
				if (existingIndex >= 0) {
					// Êõ¥Êñ∞Áé∞ÊúâÂØπËØù
					historyChats[existingIndex] = newChat;
					
					// Âè™ÊúâÂú®ÊúâÊñ∞Ê∂àÊÅØÊó∂ÊâçÈáçÊñ∞ÊéíÂ∫è
					if (hasNewMessages) {
						// Â∞ÜÊõ¥Êñ∞ÁöÑÂØπËØùÁßªÂà∞ÊúÄÂâçÈù¢
						historyChats.splice(existingIndex, 1);
						historyChats.unshift(newChat);
					}
				} else {
					// Ê∑ªÂä†Êñ∞ÂØπËØùÂà∞ÊúÄÂâçÈù¢
					historyChats.unshift(newChat);
				}
				
				uni.setStorageSync('historyChats', historyChats);
				console.log('ÂØπËØùÂ∑≤‰øùÂ≠ò:', newChat, 'ÊúâÊñ∞Ê∂àÊÅØ:', hasNewMessages);
			},
			
			// ÊòæÁ§∫ÂõæÁâáÈÄâÊã©ÈÄâÈ°π
			showImageOptions() {
				uni.showActionSheet({
					itemList: ['ÊãçÁÖß', '‰ªéÁõ∏ÂÜåÈÄâÊã©'],
					success: (res) => {
						if (res.tapIndex === 0) {
							this.takePhoto();
						} else if (res.tapIndex === 1) {
							this.chooseImage();
						}
					}
				});
			},
			
			// ÊãçÁÖß
			takePhoto() {
				uni.chooseImage({
					count: 1,
					sourceType: ['camera'],
					success: (res) => {
						this.handleImageSelected(res.tempFilePaths[0]);
					},
					fail: (err) => {
						console.log('ÊãçÁÖßÂ§±Ë¥•:', err);
						uni.showToast({
							title: 'ÊãçÁÖßÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			// ‰ªéÁõ∏ÂÜåÈÄâÊã©ÂõæÁâá
			chooseImage() {
				uni.chooseImage({
					count: 1,
					sourceType: ['album'],
					success: (res) => {
						this.handleImageSelected(res.tempFilePaths[0]);
					},
					fail: (err) => {
						console.log('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•:', err);
						uni.showToast({
							title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			// Â§ÑÁêÜÈÄâ‰∏≠ÁöÑÂõæÁâá
			handleImageSelected(imagePath) {
				// Ê∑ªÂä†Áî®Êà∑ÂõæÁâáÊ∂àÊÅØ
				this.addMessage({
					type: 'user',
					content: '[ÂõæÁâá]',
					time: this.getCurrentTime(),
					image: imagePath
				});
				
				// ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
				this.isLoading = true;
				
				// Ê®°ÊãüAIËØÜÂà´ÂõûÂ§ç
				setTimeout(() => {
					this.getImageAnalysis(imagePath);
				}, 1500);
			},
			
			// Ëé∑ÂèñÂõæÁâáÂàÜÊûêÁªìÊûú
			getImageAnalysis(imagePath) {
				// ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÁúüÂÆûÁöÑAIÂõæÂÉèËØÜÂà´API
				// ÁõÆÂâç‰ΩøÁî®Ê®°ÊãüÂõûÂ§ç
				const responses = [
					'Ê†πÊçÆÂõæÁâáÂàÜÊûêÔºåËøôÂèØËÉΩÊòØÊüêÁßçÂÆ≥Ëô´„ÄÇÂª∫ËÆÆÊÇ®ÈááÂèñÁõ∏Â∫îÁöÑÈò≤Ê≤ªÊé™ÊñΩ„ÄÇ',
					'ÂõæÁâá‰∏≠ÁöÑÂÆ≥Ëô´ÁâπÂæÅÊòéÊòæÔºåÂª∫ËÆÆ‰ΩøÁî®‰∏ì‰∏öÊùÄËô´ÂâÇËøõË°åÂ§ÑÁêÜ„ÄÇ',
					'ËøôÁúãËµ∑Êù•ÂÉèÊòØÂ∏∏ËßÅÁöÑÂÜú‰∏öÂÆ≥Ëô´ÔºåÈúÄË¶ÅÂèäÊó∂Èò≤Ê≤ª‰ª•ÈÅøÂÖçÊâ©Êï£„ÄÇ'
				];
				
				const randomResponse = responses[Math.floor(Math.random() * responses.length)];
				
				// Ê∑ªÂä†AIÂõûÂ§ç
				this.addMessage({
					type: 'ai',
					content: randomResponse,
					time: this.getCurrentTime()
				});
				
				this.isLoading = false;
				
				// ‰øùÂ≠òÂØπËØùÂà∞ÂéÜÂè≤ËÆ∞ÂΩï
				this.saveCurrentChat();
			},
			
			// ÂèëÈÄÅÊ∂àÊÅØ
			sendMessage() {
				if (!this.inputMessage.trim() || this.isLoading) {
					return;
				}
				
				// Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
				this.addMessage({
					type: 'user',
					content: this.inputMessage,
					time: this.getCurrentTime()
				});
				
				const userQuestion = this.inputMessage;
				this.inputMessage = '';
				
				// ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
				this.isLoading = true;
				
				// Ê®°ÊãüAIÂõûÂ§çÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠Â∫îËØ•Ë∞ÉÁî®ÁúüÂÆûÁöÑAPIÔºâ
				setTimeout(() => {
					this.getAIResponse(userQuestion);
				}, 1000);
			},
			
			// Ëé∑ÂèñAIÂõûÂ§ç
			getAIResponse(question) {
				// ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÁúüÂÆûÁöÑAI API
				// ÁõÆÂâç‰ΩøÁî®Ê®°ÊãüÂõûÂ§ç
				let response = '';
				
				if (question.includes('ËØÜÂà´') || question.includes('ÂÆ≥Ëô´')) {
					response = 'ÊàëÂèØ‰ª•Â∏ÆÊÇ®ËØÜÂà´ÂêÑÁßçÂÆ≥Ëô´„ÄÇËØ∑‰∏ä‰º†ÂÆ≥Ëô´ÂõæÁâáÔºåÊàë‰ºö‰∏∫ÊÇ®ÂàÜÊûêÂÆ≥Ëô´Á±ªÂûãÂπ∂Êèê‰æõÈò≤Ê≤ªÂª∫ËÆÆ„ÄÇ';
				} else if (question.includes('Èò≤Ê≤ª') || question.includes('Â§ÑÁêÜ')) {
					response = 'ÈíàÂØπ‰∏çÂêåÂÆ≥Ëô´ÔºåÊàë‰ª¨Êúâ‰∏çÂêåÁöÑÈò≤Ê≤ªÊñπÊ≥ï„ÄÇÂª∫ËÆÆÊÇ®ÂÖà‰∏ä‰º†ÂÆ≥Ëô´ÂõæÁâáËøõË°åËØÜÂà´ÔºåÁÑ∂ÂêéÊàë‰ºö‰∏∫ÊÇ®Êèê‰æõÂÖ∑‰ΩìÁöÑÈò≤Ê≤ªÊñπÊ°à„ÄÇ';
				} else if (question.includes('ÂõæÁâá') || question.includes('‰∏ä‰º†')) {
					response = 'ÊÇ®ÂèØ‰ª•ÈÄöËøáÁÇπÂáªËæìÂÖ•Ê°ÜÊóÅËæπÁöÑÂä†Âè∑ÊåâÈíÆÊù•‰∏ä‰º†ÂÆ≥Ëô´ÂõæÁâá„ÄÇÊîØÊåÅÊãçÁÖßÂíå‰ªéÁõ∏ÂÜåÈÄâÊã©ÂõæÁâá„ÄÇ';
				} else {
					response = 'ÊàëÊòØ‰∏ì‰∏öÁöÑÂÆ≥Ëô´ËØÜÂà´Âä©ÊâãÔºåÂèØ‰ª•Â∏ÆÊÇ®ËØÜÂà´ÂÆ≥Ëô´„ÄÅÊèê‰æõÈò≤Ê≤ªÂª∫ËÆÆ„ÄÇËØ∑ÂëäËØâÊàëÊÇ®ÈúÄË¶Å‰ªÄ‰πàÂ∏ÆÂä©ÔºåÊàñËÄÖ‰∏ä‰º†ÂÆ≥Ëô´ÂõæÁâáËøõË°åËØÜÂà´„ÄÇ';
				}
				
				// Ê∑ªÂä†AIÂõûÂ§ç
				this.addMessage({
					type: 'ai',
					content: response,
					time: this.getCurrentTime()
				});
				
				this.isLoading = false;
				
				// ‰øùÂ≠òÂØπËØùÂà∞ÂéÜÂè≤ËÆ∞ÂΩïÔºàÂè™ÊúâÂú®ÊúâÊñ∞Ê∂àÊÅØÊó∂ÊâçÊõ¥Êñ∞Ôºâ
				this.saveCurrentChat();
			},
			
			// Ê∑ªÂä†Ê∂àÊÅØÂà∞ÂàóË°®
			addMessage(message) {
				this.messageList.push(message);
				this.$nextTick(() => {
					// Âª∂ËøüÊªöÂä®Á°Æ‰øùÊñ∞Ê∂àÊÅØÂÆåÂÖ®Ê∏≤Êüì
					setTimeout(() => {
						this.scrollToBottom();
					}, 100);
				});
			},
			
			// ÊªöÂä®Âà∞Â∫ïÈÉ®
			scrollToBottom() {
				this.$nextTick(() => {
					// ‰ΩøÁî®scroll-into-viewÊªöÂä®Âà∞ÊúÄÂêé‰∏Ä‰∏™Ê∂àÊÅØ
					if (this.messageList.length > 0) {
						const lastIndex = this.messageList.length - 1;
						this.scrollIntoView = `msg-${lastIndex}`;
					} else {
						// Â¶ÇÊûúÊ≤°ÊúâÊ∂àÊÅØÔºåÊªöÂä®Âà∞È°∂ÈÉ®
						this.scrollTop = 0;
					}
				});
			},
			
			// Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥
			getCurrentTime() {
				const now = new Date();
				const hours = now.getHours().toString().padStart(2, '0');
				const minutes = now.getMinutes().toString().padStart(2, '0');
				return `${hours}:${minutes}`;
			},
			
			// Ëé∑ÂèñÂΩìÂâçÊó•ÊúüÊó∂Èó¥
			getCurrentDateTime() {
				const now = new Date();
				const year = now.getFullYear();
				const month = (now.getMonth() + 1).toString().padStart(2, '0');
				const day = now.getDate().toString().padStart(2, '0');
				const hours = now.getHours().toString().padStart(2, '0');
				const minutes = now.getMinutes().toString().padStart(2, '0');
				return `${year}-${month}-${day} ${hours}:${minutes}`;
			},
			
			// ÊêúÁ¥¢ËæìÂÖ•Â§ÑÁêÜ
			onSearchInput() {
				if (this.searchKeyword.trim()) {
					this.performSearch();
				} else {
					this.clearSearch();
				}
			},
			
			// ÊâßË°åÊêúÁ¥¢
			performSearch() {
				if (!this.searchKeyword.trim()) {
					this.clearSearch();
					return;
				}
				
				const keyword = this.searchKeyword.toLowerCase();
				this.searchResults = [];
				
				this.messageList.forEach((message, index) => {
					if (message.content && message.content.toLowerCase().includes(keyword)) {
						this.searchResults.push(index);
					}
				});
				
				if (this.searchResults.length > 0) {
					// ÊªöÂä®Âà∞Á¨¨‰∏Ä‰∏™ÊêúÁ¥¢ÁªìÊûú
					this.scrollToMessage(this.searchResults[0]);
					uni.showToast({
						title: `ÊâæÂà∞ ${this.searchResults.length} ‰∏™ÁªìÊûú`,
						icon: 'none'
					});
				} else {
					uni.showToast({
						title: 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÂÜÖÂÆπ',
						icon: 'none'
					});
				}
			},
			
			// Ê∏ÖÈô§ÊêúÁ¥¢
			clearSearch() {
				this.searchKeyword = '';
				this.searchResults = [];
			},
			
			// Ê£ÄÊü•Ê∂àÊÅØÊòØÂê¶È´ò‰∫Æ
			isMessageHighlighted(message) {
				if (!this.searchKeyword.trim()) return false;
				const index = this.messageList.indexOf(message);
				return this.searchResults.includes(index);
			},
			
			// È´ò‰∫ÆÊêúÁ¥¢ÊñáÊú¨ÔºàÂ∑≤ÁßªÈô§ÔºåÊîπÁî®CSSÊ†∑ÂºèÂÆûÁé∞Ôºâ
			highlightSearchText(text) {
				return text; // Áõ¥Êé•ËøîÂõûÂéüÊñáÊú¨Ôºå‰ΩøÁî®CSSÊ†∑ÂºèÂÆûÁé∞È´ò‰∫Æ
			},
			
			// ÊªöÂä®Âà∞ÊåáÂÆöÊ∂àÊÅØ
			scrollToMessage(index) {
				this.$nextTick(() => {
					this.scrollIntoView = `msg-${index}`;
				});
			}
		}
	}
</script>

